<template>
  <div class="headIndexLogin">
    <!--  -->
    <div class="top-bar">
      <div class="wrap">
        <div class="top-left">
          <!--  -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeWebPlace">
            <span>
              {{place}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item, index) in webPlace"
                :key="index" :command="item.value">
                {{item.place}}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--  -->
          <el-dropdown v-if='userName' class='top-dropdown' size='small' trigger="hover" @command="logoutFunc">
            <span>
              {{userName}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown" style='width: 100px'>
              <el-dropdown-item command="1">
                
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- <router-link v-if='userName' class='bar-link red' to=''>{{userName}}<i class="el-icon-arrow-down el-icon--right"></i></router-link> -->
          <router-link v-if='!userName' class='bar-link red' to='/login'></router-link>
          <router-link v-if='!userName' class='bar-link' to='/register'></router-link>
          <router-link class='bar-link' to='/login'></router-link>
        </div>
        <div class="top-right">
          <!--  -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeMytaobao">
            <span>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1"></el-dropdown-item>
              <el-dropdown-item command="2"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--  -->
          <router-link class='bar-link' to=''><i class='fa fa-cart-plus'></i><span></span><i class='el-icon-arrow-down'></i></router-link>
          <!--  -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeCollect">
            <span>
              <i class='fa fa-star'></i><span class='tab'></span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1"></el-dropdown-item>
              <el-dropdown-item command="2"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--  -->
          <router-link class='bar-link' to=''><span style='margin-right: 10px;'></span>|</router-link>
          <!--  -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeSellect">
            <span>
              <span class='tab'></span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1"></el-dropdown-item>
              <el-dropdown-item command="2"></el-dropdown-item>
              <el-dropdown-item command="3"></el-dropdown-item>
              <el-dropdown-item command="4"></el-dropdown-item>
              <el-dropdown-item command="5"></el-dropdown-item>
              <el-dropdown-item command="6"></el-dropdown-item>
              <el-dropdown-item command="7"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--  -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeCustom">
            <span>
              <span class='tab'></span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1"></el-dropdown-item>
              <el-dropdown-item command="2"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--  -->
          <el-dropdown class='top-dropdown top-nav-bar' size='small' trigger="hover" @command="changeSellect">
            <span>
              <i class='fa fa-bars'></i><span class='tab'></span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu style='width: 1190px; padding: 10px 0' slot="dropdown">
              <el-row class='nav-row'>
                <!--  -->
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-1'></h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.theme' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-2'></h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.feature' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-3'>APP</h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.aliapp' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-4'></h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.recommend' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
              </el-row>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'headIndexLogin',
  computed: {
    ...mapGetters(['webPlace', 'headNav', 'userName'])
  },
  data () {
    return {
      place: '',
      searchTab: 0,
      search: '',
      moreLinks: [
        { name: '', id: 1 },
        { name: '', id: 2 },
        { name: 'T', id: 3 },
        { name: '', id: 4 },
        { name: '', id: 5 },
        { name: '', id: 6 },
        { name: '', id: 7 },
        { name: '', id: 8 },
        { name: '', id: 9 }
      ],
      moreLink: 0
    }
  },
  methods: {
    // top
    // 
    changeWebPlace(command) {
      this.webPlace.filter(item => {
        if (item.value === command) {
          this.place = item.place
        }
      })
    },
    // 
    logoutFunc(command) {
      if (command === '1') {
        this.$message.success('')
        this.$cookie.set('userName', '')
        this.$router.push('/login')
      }
    },
    // 
    changeMytaobao(command) {
      alert(command)
    },
    // 
    changeCollect(command) {
      alert(command)
    },
    // 
    changeSellect(command) {
      alert(command)
    },
    // 
    changeCustom(command) {
      alert(command)
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
// 
.headIndexLogin{
  background: #fff;
  .top-bar {
    width: 100%;
    height: 36px;
    background: #f5f5f5;
    border-bottom: 1px solid #eee;
    .wrap {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      .bar-link {
        padding: 0 3px;
        &.red, &:hover {
          color: $color-red;
        }
      }
      .top-dropdown {
        height: 36px;
        line-height: 36px;
        padding: 0 3px;
        font-size: 12px;
        &:hover {
          cursor: pointer;
          span {
            color: $color-red
          }
        }
        span.tab {
          display: inline-block;
          margin-left: 3px;
          margin-right: 2px;
        }
      }
      .top-left {
        width: 400px;
        height: 36px;
      }
      .top-right {
        display: flex;
        justify-content: flex-end;
        width: 700px;
        height: 36px;
        line-height: 36px;
        .bar-link {
          margin-left: 5px;
          margin-right: 2px;
          span {
            display: inline-block;
            margin-left: 3px;
            margin-right: 5px;
          }
        }
      }

    }
  }
}
// 
.nav-row {
  display: flex;
  .nav-col {
    border-right: 1px solid #eee;
    padding-left: 7px;
    align-self: stretch;
  }
  .nav-title {
    padding-left: 10px;
  }
  .nav-title-1 {
    color: #fc4200;
  }
  .nav-title-2 {
    color: #9fb838;
  }
  .nav-title-3 {
    color: #d75696;
  }
  .nav-title-4 {
    color: #2e9fdb;
  }
  .nav-items {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 10px;
  }
  .nav-item {
    flex: 0 0 40px;
  }
}
</style>



