<template>
  <div class="register">
    <headIndexLogin />
    
    <div class="wrap">
      <div class='reg-head'>
        <router-link class='reg-logo' to='/'><img src="@/assets/logo.png" alt=""></router-link>
        <span></span>
      </div>
      <!--  -->
      <div class="reg-nav">
        <div class="item active">
          <span class='count'>1</span>
          <span class='title'></span>
        </div>
        <div class="item">
          <span class='count'>2</span>
          <span class='title'></span>
        </div>
        <div class="item">
          <span class='count'>3</span>
          <span class='title'></span>
        </div>
        <div class="item">
          <span class='count'>4</span>
          <span class='title'></span>
        </div>
      </div>
      <!--  -->
      <div class="reg-con">
        <div class="reg-con-item">
          <components :is='componentNames[regState]'></components>
          <!-- <reg1SetName /> -->
        </div>
      </div>
    </div>
    <footLogin />
    
    <!--  -->
    <div v-if='showAgree' class="register-agree">
      <div class="box">
        <div class="title">
          <p></p>
          <router-link to='/'><i class='fa fa-close'></i></router-link>
        </div>
        <div class="con">
          <p><span class='bold'></span><span class='bold under'></span></p>
          <p>1<span class='bold under'></span></p>
          <p>2<span class='bold under'></span></p>
          <p>3<span class='bold under'></span></p>

          <p></p>

          <p><span class='bold'></span></p>
          <p><span class='bold under'></span></p>
          <router-link to=''></router-link><br/>
          <router-link to=''></router-link><br/>
          <router-link to=''></router-link>
        </div>
        <button class='btn' @click='hideAgree'></button>
      </div>
      
    </div>
    
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import headIndexLogin from '@/components/header/headIndexLogin'
import footLogin from '@/components/footer/footLogin'

import reg1SetName from './reg1SetName'
import reg2GetCode from './reg2GetCode'
export default {
  name: 'register',
  components: {
    headIndexLogin,
    footLogin,
    // register tabs
    reg1SetName,
    reg2GetCode
  },
  data () {
    return {
      componentNames: ['reg1SetName', 'reg2GetCode'],
      showAgree: false,
      viewBar: footLogin
    }
  },
  computed: {
    ...mapGetters(['regState'])
  },
  created() {
    
  },
  mounted() {
  },
  methods: {
    hideAgree() {
      this.showAgree = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '@/assets/css/public.scss';
.register {
  width: 100%;
  background: #fff;
  .wrap {
    // logo
    .reg-head {
      display: flex;
      justify-content: flex-start;
      margin: 20px 0;
      .reg-logo {
        margin-right: 12px;
        line-height: 42px;
        img {
          height: 42px;
        }
      }
      span {
        display: inline-block;
        font-size: 20px;
        margin-top: 10px;
      }
    }

    // nav
    .reg-nav {
      height: 40px;
      border-bottom: 2px solid #e6e6e6;
      display: flex;
      justify-content: center;
      // box-sizing: border-box;
      .item {
        width: 180px;
        height: 40px;
        border-bottom: 2px solid #e6e6e6;
        text-align: center;
        span {
          display: inline-block;
          margin-top: 4px;
          line-height: 24px;
          height: 24px;
          font-size: 14px;
          font-weight: bold;
          color: #999;
        }
        .count {
          width: 24px;
          border-radius: 12px;
          background: #e6e6e6;
          margin-right: 4px;
          text-align: center;
          color: #fff;
          
        }
        &.active {
          border-bottom: 2px solid $color-orange;
          .title {
            color: #333;
          }
          .count {
            background: $color-orange
          }

        }
      }
    }

    // form
    .reg-con {
      width: 500px;
      margin: 60px auto 160px;
    }
  }
  // 
  .register-agree {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    
    background: rgba(0, 0, 0, .2);
    .box {
      width: 750px;
      height: 440px;
      background: #fff;
      position: fixed;
      top: calc(50% - 220px);
      left: calc(50% - 375px);
      padding: 20px;
      box-sizing: border-box;
      color: #666;
      font-size: 13px;
      border-top: 2px solid $color-red;
      .title {
        color: #000;
        font-size: 18px;
        position: relative;
        margin-bottom: 16px;
        a {
          display: inline-block;
          position: absolute;
          right: 0px;
          top: 0px;
        }
      }

      .con {
        p {
          line-height: 1.4;
          margin-bottom: 10px;

          span.under {
            text-decoration: underline;
          }
          span.bold {
            font-weight: bold;
            color: #333;
          }
        }
        a {
          display: inline-block;
          margin-bottom: 8px;
          line-height: 1.6;
          color: $color-orange;
          &:hover {
            text-decoration: underline;
          }
        }
        
      }
      .btn {
        display: block;
        margin: 0 auto;
        padding: 0 20px;
        background: $color-orange;
        color: #fff;
        line-height: 32px;
        width: 140px;
        border-radius: 3px;
        transition: .4s;
        &:hover {
          background: $color-red;
        }
      }
    }
  }
}
</style>



