import router from './router'
import store from './store'
// import NProgress from 'nprogress' // progress 
// import 'ngrogress/ngrogress.css' // progress 

router.beforeEach((to, from, next) => {
  let userRole = store.getters.userRole
  // 
  if (!userRole) {
    // 
    if (to.path === '/user') {
      alert('')
      next('/login')
    } else {
      next()
    }
  } else {
    // 
    // 
    store.dispatch('GenerateRoutes', userRole).then(() => {
      // 
      router.addRoutes(store.getters.addRouters)
      next({ ...to }) // hackaddRoutes
    })
    next()
  }
})



