import Mock from 'mockjs'

import classifyAPI from './index/bannerClass'
import ggsAPI from './index/getGgs'
import getAgjAPI from './index/getAgj'
import getRushBuyAPI from './index/getRushBuy'
import getMrhdAPI from './index/getMrhd'
import getTbzbAPI from './index/getTbzb'

Mock.setup({
  timeout: '350-600'
})

// mock  api url
// 
Mock.mock(/\/getClassify/, 'post', classifyAPI.getClassify)
Mock.mock(/\/getGgs/, 'get', ggsAPI.getGgs) // 
Mock.mock(/\/getAgj/, 'get', getAgjAPI.getAgj) // 
Mock.mock(/\/getRushBuy/, 'get', getRushBuyAPI.getRushBuy) // 
Mock.mock(/\/getMrhd/, 'get', getMrhdAPI.getMrhd) // 
Mock.mock(/\/getTbzb/, 'get', getTbzbAPI.getTbzb) // 

export default Mock



