export default {
  phoneReg: /^[1][3,4,5,7,8][0-9]{9}$/, // 
  gdPhoneReg: /^0\d{2,3}-[1-9]\d{6,7}$/, //  '-'
  QQReg: /^[1-9][0-9]{4,}$/, // qq
  emailReg: /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/, // 
  yzReg: /^[1-9][0-9]{5}$/, // 
  // czReg: /^(\d{3,4}-)?\d{7,8}$/, // 
  yhReg: /^(\d{16}|\d{19})$/, // 
  sfzhReg: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/, // 
  xydmReg: /[^_IOZSVa-z\W]{2}\d{6}[^_IOZSVa-z\W]{10}$/g, // 
  cphReg: /^[A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9]{1}$/
}



